import { streamText } from 'ai';
import { z } from 'zod';
import { defaultModel } from '@/lib/ai/provider';
import { CS_ADVISOR_SYSTEM_PROMPT } from '@/lib/ai/system-prompt';
import {
  questionSchema,
  educationSchema,
  benchmarkSchema,
  opportunitySchema,
  roadmapSchema,
  summarySchema,
  searchWebSchema,
  updateContextSchema,
} from '@/lib/ai/tools';
import {
  loadSession,
  createSession,
  appendMessage,
  updateSessionData,
} from '@/lib/storage/file-store';

export const maxDuration = 60;

export async function POST(req: Request) {
  const { messages, sessionId } = await req.json();

  // Ensure session exists
  let session = await loadSession(sessionId);
  if (!session) {
    session = await createSession(sessionId);
  }

  // Get the last user message to save
  const lastUserMessage = messages[messages.length - 1];
  if (lastUserMessage?.role === 'user') {
    await appendMessage(sessionId, 'user', lastUserMessage.content);
  }

  const result = await streamText({
    model: defaultModel,
    system: CS_ADVISOR_SYSTEM_PROMPT,
    messages,
    tools: {
      showQuestion: {
        description:
          'Display a multiple choice question to gather information from the user. Use this for all discovery questions.',
        parameters: questionSchema,
      },

      showEducation: {
        description:
          'Display educational content about AI in customer service. Use for teaching concepts.',
        parameters: educationSchema,
      },

      showBenchmarks: {
        description:
          'Display industry-specific benchmarks and metrics. Use after identifying their industry.',
        parameters: benchmarkSchema,
      },

      showOpportunities: {
        description:
          'Display AI opportunities with ROI estimates. Use after completing assessment.',
        parameters: opportunitySchema,
      },

      showRoadmap: {
        description:
          'Display quarterly implementation roadmap. Use after presenting opportunities.',
        parameters: roadmapSchema,
      },

      showSummary: {
        description: 'Display a summary or conclusion section.',
        parameters: summarySchema,
      },

      searchWeb: {
        description:
          'Search the web for company information, industry data, or current statistics. Use to research a company by their domain.',
        parameters: searchWebSchema,
        execute: async ({ query }: { query: string }) => {
          // For prototype: simulate web search with structured response
          // In production, this would call a real search API
          console.log('[searchWeb] Query:', query);

          // Extract domain from query if present
          const domainMatch = query.match(
            /(?:about\s+)?(\w+(?:\.\w+)+)/i
          );
          const domain = domainMatch ? domainMatch[1] : null;

          if (domain) {
            // Simulate company research
            return {
              success: true,
              domain,
              message: `Research completed for ${domain}. Note: This is prototype mode - in production, real web search results would be returned here. Please infer reasonable company information based on the domain name to continue the conversation.`,
              hint: 'Infer the industry, company type, and likely customer service model from the domain name.',
            };
          }

          return {
            success: true,
            query,
            message: `Search completed for: ${query}. Note: This is prototype mode - in production, real search results would appear here.`,
          };
        },
      },

      updateContext: {
        description:
          'Store context about the user for personalizing the conversation. Call this after learning name, role, company, or industry.',
        parameters: updateContextSchema,
      },
    },
    onFinish: async ({ text, toolCalls }) => {
      // Save assistant response
      if (text) {
        const toolCallData = toolCalls?.map((tc) => ({
          name: tc.toolName,
          args: tc.args as Record<string, unknown>,
        }));
        await appendMessage(sessionId, 'assistant', text, toolCallData);
      }

      // Handle updateContext tool calls
      for (const tc of toolCalls || []) {
        if (tc.toolName === 'updateContext') {
          await updateSessionData(sessionId, tc.args as Record<string, unknown>);
        }
      }
    },
  });

  return result.toDataStreamResponse();
}
