import fs from 'fs/promises';
import path from 'path';

const DATA_DIR = path.join(process.cwd(), '..', 'data', 'sessions');

export interface SessionData {
  id: string;
  createdAt: string;
  updatedAt: string;
  phase: 'discovery' | 'education' | 'assessment' | 'results' | 'complete';
  companyContext?: {
    industry: string;
    customerType: string;
    size?: string;
  };
  userContext?: {
    role: string;
    interestPath: string;
  };
  assessmentData?: {
    ticketVolume?: string;
    channels?: string[];
    teamSize?: string;
    primaryPainPoint?: string;
    platform?: string;
    dataState?: string;
  };
  messages: Array<{
    role: 'user' | 'assistant';
    content: string;
    timestamp: string;
    toolCalls?: Array<{
      name: string;
      args: Record<string, unknown>;
    }>;
  }>;
}

export async function ensureDataDir(): Promise<void> {
  await fs.mkdir(DATA_DIR, { recursive: true });
}

export async function saveSession(sessionId: string, data: SessionData): Promise<void> {
  await ensureDataDir();
  const filePath = path.join(DATA_DIR, `${sessionId}.json`);
  await fs.writeFile(filePath, JSON.stringify(data, null, 2));
}

export async function loadSession(sessionId: string): Promise<SessionData | null> {
  try {
    const filePath = path.join(DATA_DIR, `${sessionId}.json`);
    const content = await fs.readFile(filePath, 'utf-8');
    return JSON.parse(content);
  } catch {
    return null;
  }
}

export async function createSession(sessionId: string): Promise<SessionData> {
  const session: SessionData = {
    id: sessionId,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
    phase: 'discovery',
    messages: [],
  };
  await saveSession(sessionId, session);
  return session;
}

export async function appendMessage(
  sessionId: string,
  role: 'user' | 'assistant',
  content: string,
  toolCalls?: Array<{ name: string; args: Record<string, unknown> }>
): Promise<void> {
  const session = await loadSession(sessionId);
  if (!session) {
    throw new Error(`Session ${sessionId} not found`);
  }

  session.messages.push({
    role,
    content,
    timestamp: new Date().toISOString(),
    toolCalls,
  });
  session.updatedAt = new Date().toISOString();

  await saveSession(sessionId, session);
}

export async function updateSessionData(
  sessionId: string,
  updates: Partial<Omit<SessionData, 'id' | 'createdAt' | 'messages'>>
): Promise<void> {
  const session = await loadSession(sessionId);
  if (!session) {
    throw new Error(`Session ${sessionId} not found`);
  }

  Object.assign(session, updates);
  session.updatedAt = new Date().toISOString();

  await saveSession(sessionId, session);
}

export async function listSessions(): Promise<string[]> {
  try {
    await ensureDataDir();
    const files = await fs.readdir(DATA_DIR);
    return files
      .filter(f => f.endsWith('.json'))
      .map(f => f.replace('.json', ''));
  } catch {
    return [];
  }
}
