import { z } from 'zod';

// Tool parameter schemas
export const questionSchema = z.object({
  header: z.string().describe('Short label for the question category'),
  question: z.string().describe('The full question to ask the user'),
  inputType: z.enum(['select', 'text']).optional().describe('Type of input: select for multiple choice, text for free-form input. Defaults to select.'),
  placeholder: z.string().optional().describe('Placeholder text for text input'),
  options: z.array(z.object({
    id: z.string().describe('Unique identifier for this option'),
    label: z.string().describe('Display text for the option'),
    description: z.string().optional().describe('Additional context for the option'),
  })).optional().describe('2-4 answer options (required for select inputType)'),
  allowMultiple: z.boolean().optional().describe('Allow selecting multiple options (only for select inputType)'),
});

export const educationSchema = z.object({
  title: z.string().describe('Topic title'),
  content: z.string().describe('Main educational content (markdown supported)'),
  keyPoints: z.array(z.string()).optional().describe('Bullet point key takeaways'),
  drillDownOptions: z.array(z.string()).optional().describe('Topics user can explore deeper'),
});

export const benchmarkSchema = z.object({
  industry: z.string().describe('Industry name'),
  metrics: z.array(z.object({
    name: z.string().describe('Metric name'),
    value: z.string().describe('Metric value'),
    context: z.string().optional().describe('What this means'),
  })).describe('Industry benchmark metrics'),
  insights: z.array(z.string()).optional().describe('Key insights about this industry'),
});

export const opportunitySchema = z.object({
  tactical: z.array(z.object({
    id: z.string(),
    name: z.string(),
    description: z.string(),
    effort: z.enum(['Low', 'Medium', 'High']),
    timeline: z.string(),
    estimatedSavings: z.string(),
  })).describe('Quick wins (30-90 days)'),
  strategic: z.array(z.object({
    id: z.string(),
    name: z.string(),
    description: z.string(),
    effort: z.enum(['Low', 'Medium', 'High']),
    timeline: z.string(),
    estimatedSavings: z.string(),
    prerequisites: z.array(z.string()).optional(),
  })).describe('Longer-term initiatives (6-18 months)'),
});

export const roadmapSchema = z.object({
  quarters: z.array(z.object({
    quarter: z.string().describe('Quarter label (e.g., "Q1")'),
    title: z.string().describe('Quarter theme'),
    initiatives: z.array(z.object({
      name: z.string(),
      description: z.string(),
      milestone: z.string().optional(),
    })),
  })),
});

export const summarySchema = z.object({
  title: z.string(),
  sections: z.array(z.object({
    heading: z.string(),
    content: z.string(),
  })),
});

export const searchWebSchema = z.object({
  query: z.string().describe('Search query to find company info, industry data, or current statistics'),
});

export const updateContextSchema = z.object({
  name: z.string().optional().describe('User name'),
  role: z.string().optional().describe('User role'),
  company: z.string().optional().describe('Company name'),
  domain: z.string().optional().describe('Company domain'),
  industry: z.string().optional().describe('Industry/sub-industry'),
});

// Tool definitions for the AI
export const advisorTools = {
  showQuestion: {
    description: 'Display a multiple choice question to gather information from the user. Use this for all discovery questions.',
    parameters: questionSchema,
  },

  showEducation: {
    description: 'Display educational content about AI in customer service. Use for teaching concepts.',
    parameters: educationSchema,
  },

  showBenchmarks: {
    description: 'Display industry-specific benchmarks and metrics. Use after identifying their industry.',
    parameters: benchmarkSchema,
  },

  showOpportunities: {
    description: 'Display AI opportunities with ROI estimates. Use after completing assessment.',
    parameters: opportunitySchema,
  },

  showRoadmap: {
    description: 'Display quarterly implementation roadmap. Use after presenting opportunities.',
    parameters: roadmapSchema,
  },

  showSummary: {
    description: 'Display a summary or conclusion section.',
    parameters: summarySchema,
  },

  searchWeb: {
    description: 'Search the web for company information, industry data, or current statistics. Use to research a company by their domain.',
    parameters: searchWebSchema,
  },

  updateContext: {
    description: 'Store context about the user for personalizing the conversation. Call this after learning name, role, company, or industry.',
    parameters: updateContextSchema,
  },
};

// Type exports for components
export type QuestionParams = z.infer<typeof questionSchema>;
export type EducationParams = z.infer<typeof educationSchema>;
export type BenchmarkParams = z.infer<typeof benchmarkSchema>;
export type OpportunityParams = z.infer<typeof opportunitySchema>;
export type RoadmapParams = z.infer<typeof roadmapSchema>;
export type SummaryParams = z.infer<typeof summarySchema>;
export type SearchWebParams = z.infer<typeof searchWebSchema>;
export type UpdateContextParams = z.infer<typeof updateContextSchema>;
