import { createAnthropic } from '@ai-sdk/anthropic';

// Create Anthropic provider instance
// Uses ANTHROPIC_API_KEY from environment
export const anthropic = createAnthropic({
  // API key is automatically read from ANTHROPIC_API_KEY env var
});

// Default model for the CS Advisor
export const defaultModel = anthropic('claude-sonnet-4-20250514');

// For future: factory pattern to support multiple providers
export type AIProviderType = 'anthropic' | 'openai' | 'vertex';

export function getModel(provider: AIProviderType = 'anthropic') {
  switch (provider) {
    case 'anthropic':
      return defaultModel;
    // Future: add other providers
    // case 'openai':
    //   return openai('gpt-4o');
    // case 'vertex':
    //   return vertex('gemini-1.5-pro');
    default:
      return defaultModel;
  }
}
